/* RESET CSS */
* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* STYLES FOR ALL */
body {
	background-color: #f9fbf9;
}

.container {
	max-width: 960px;
	margin: 0 auto;
}

/* STYLES FOR HEADER (with logo) */

.unamo-header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: baseline;
	    align-items: baseline;
	padding: 40px 0;
}

.header-link {
	text-decoration: none;
	color: #08ab6b;
}

.mainApp {
	background-color: #fff;
	border-radius: 3px;
	-webkit-box-shadow: 0px 1px 3px rgba(0,0,0,0.15);
	        box-shadow: 0px 1px 3px rgba(0,0,0,0.15);


}
/* STYLES FOR MAIN APP HEADER */

.main-app-header div {
  display: inline-block;

}

.main-app-header p {
  display: inline;
  margin-left: 10px;

}

.success-msg {
  color: #07ab6b;
  margin-right: 20px;
}

.invalid-msg, .limit-msg{
  margin-top: 20px;
  font-size: 14px;
}

.invalid-msg, .limit-msg {
  color: red;
}


.add-user-btn {
	width: 150px;
	height: 40px;
	border-radius: 3px;
	background-color: #ffffff;
	border: 1px solid #07ab6b;
	color: #07ab6b;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
  margin: 20px 30px;
}

.add-user-btn:hover {
	background-color: #07ab6b;
	color: white;
}


.add-user-btn span {
	margin-left: 10px;
}
.fa-plus-circle {
	-webkit-transform: scale(1.5);
	    -ms-transform: scale(1.5);
	        transform: scale(1.5);
}


button:disabled {
  color: grey;
  border-color: grey;
  pointer-events: none;
}


/* STYLES FOR USER FORM IN HEADER */

.user-form {
    margin: 20px 30px;

}

.user-form input {
  width: 250px;
  height: 38px;
  border-radius: 3px;
  border: 1px solid #cfd3d5;
  margin-right: 10px;
  padding: 0 10px ;
  font-size: 16px;
  color: #646c73;
}

.user-form input[type="submit"] {
  width: 150px;
  height: 40px;
  background-color: #07ab6b;
  color: white;
  border: 1px solid #07ab6b;
  font-weight: bold;
  cursor: pointer;

}

.user-form .reset-btn {
  background: none;
  border: none;
  color: red;
  text-decoration: underline;
  cursor: pointer;

}




	/* STYLES FOR USER TABLE */

.users-table {
  border-collapse:collapse;
  width: 100%;
}
.users-header {
    background-color: #e9ebef;
}

th {
  text-align: left;
  padding: 5px 30px;
  font-size: 12px;


}
td {
    padding: 20px 30px;
    font-size: 14px;
}

.users-list > tr:nth-child(2n) {
  background-color: #fbfbfb;
}

.remove-user-btn {
    background: none;
   border: none;
   outline: none;
   cursor: pointer;
}

.fa-times {
  -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
  color: #d1d6d7;

}

.fa-sort-down {
  padding-left: 10px;
  -webkit-transform: scale(1.5) translateY(-20%);
      -ms-transform: scale(1.5) translateY(-20%);
          transform: scale(1.5) translateY(-20%);
}

.fa-sort-up {
    padding-left: 10px;
    -webkit-transform: scale(1.5) translateY(20%);
        -ms-transform: scale(1.5) translateY(20%);
            transform: scale(1.5) translateY(20%);
}



